.entity-container {
    border: 2px solid black;
    border-radius: 30px;
    width: 50px;
    height: 50px;
    background-size: cover;
    cursor: none;
    margin: 0 auto;
}

.entity-block {
    margin: 0 auto;
    max-width: 500px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 15px
}

.entity-block::after {
    content: "";
    min-width: 110px;
    flex: auto;
}

.tooltip-text {
    padding: 10px;
    border-radius: 5px;
    background-color: black;
    color: white;
    position: absolute;
    z-index: 100;
    max-width: 250px;
}

.title-entity {
    min-width: 110px;
    min-height: 110px;
    max-width: 110px;
}

.title-entity p {
    margin: 0;
}

.main-logo {
    width: 250px;
    cursor: none;
    display: block;
    margin: 0 auto;
}

.search {
    margin-top: 15px;
    margin-bottom: 15px;
    max-width: 500px;
    width: 80%;
    font-size: 1.5rem;
}

.main-logo:hover {
    width: 255px;
    transition-duration: 0.5s;
}

.entity-container:hover {
    width: 53px;
    height: 53px;
    transition-duration: 0.5s;
}
